import React from 'react';
/**
 * @param  {string} result 
 * 
 * function returns the result based on the number it all adds up to
 */
function Result({ result }) {


    if (result >= 3 && result <= 4) {
        return (
            <div>
                <p>Hi, seems like you are Randall Pearson</p>
                <img
                    src="data:image/jpeg;base64,"
                    alt="randall-pearson" />
            </div>
        )
    }

    if (result >= 5 && result <= 6) {
        return (
            <div>
                <p>Hi, seems like you are Kate Pearson</p>
                <img
                    src="data:image/jpeg;base64,"
                    alt="kate-pearson" />
            </div>
        )
    }

    if (result >= 7 && result <= 9) {
        return (
            <div>
                <p>Hi, seems like you are Kevin Pearson</p>
                <img
                    src="data:image/jpeg;base64,"
                    alt="kevin-pearson" />
            </div>
        )
    }

    if (result >= 10 && result <= 12) {
        return (
            <div>
                <p>Hi, seems like you are Beth Pearson</p>
                <img
                    src="data:image/jpeg;base64,"
                    alt="beth-pearson" />
            </div>
        )
    }


}

export default Result;